import { Component, OnInit } from '@angular/core';
import {ApiService} from './api.service';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{
  constructor(private servizio:ApiService){}
  ngOnInit(){
    this.servizio.read().subscribe((policy : string) =>{
      console.log(policy[0]['s']);
      document.getElementById('prova').innerHTML = policy[0]['s'];
    });

  }
  title = 'app';
}
